import React, { useState, useEffect } from 'react';
import axios from 'axios';
import qs from 'qs'; 
import ModalVideo from 'react-modal-video'

import Panel from './components/panel/index.jsx';
import Banner from './components/banner/index.jsx';
import LoginComponent from './components/login/index.jsx';
import Serach from './components/search/view.jsx'
import Footer from './components/footer/index.jsx'

import Pie from './components/charts/pie.jsx'
import Line from './components/charts/line.jsx' 
import Radar from './components/charts/radar.jsx' 

import Radar2 from './components/charts/radar2.jsx'  
import Bar2 from './components/charts/bar2.jsx'
import Pie2 from './components/charts/pie2.jsx'
import Line2 from './components/charts/line2.jsx'


import Bar3 from './components/charts/bar3.jsx'

import './App.css';
import '../node_modules/react-modal-video/scss/modal-video.scss';
import { Row, Col } from 'antd';

function App() {

  const [isOpen, setIsOpen] = useState(true);
  const [dataset, setDataset] = useState(2);
  const [apiLoad, setApiLoad] = useState(false);
  const [dataApi, setDataApi] = useState({});
  
  const ApiData = 
  {
    cantidadDatos:0,
    tipo_usuario:{
      uno: 0,
      dos: 0,
      tres: 0
    },
    calificacionApoyo:{
      excelente: 0,
      bueno: 0,
      regular: 0,
      malo: 0,
      deficiente: 0
    },
    tiempoRespuesta:{
      excelente: 0,
      bueno: 0,
      regular: 0,
      malo: 0,
      deficiente: 0
    },
    calidadInsumos: {
      excelente: 0,
      bueno: 0,
      regular: 0,
      malo: 0,
      deficiente: 0
    },
    mecanismosComunicacion:{
      excelente: 0,
      bueno: 0,
      regular: 0,
      malo: 0,
      deficiente: 0
    }
  }

  
  useEffect(() => {
    axios({
      url: 'http://redcap.unal.edu.co/redcap/api/',
      method: 'post',
      data: qs.stringify({
        token: '57CC06B9A13EE0C8F2934728ABC91850',
        content: 'record',
        format: 'json',
        type: 'flat',
        rawOrLabel: 'raw',
        rawOrLabelHeaders: 'raw',
        exportCheckboxLabel: 'false',
        exportSurveyFields: 'false',
        exportDataAccessGroups: 'false',
        returnFormat: 'json'
      }),
      headers:{
        'Content-Type': 'application/x-www-form-urlencoded',
        'Accept': 'application'
      }
    })
      .then((result) =>{
        result.data.map(dato=> {


          ApiData.cantidadDatos+=1;
          if (dato.tipo_de_usuario==='1'){
            ApiData.tipo_usuario.uno += 1;
          }else if(dato.tipo_de_usuario==='2'){
            ApiData.tipo_usuario.dos +=1 ;
          }else if (dato.tipo_de_usuario==='3'){
            ApiData.tipo_usuario.tres +=1;
          }

          if(dato.c_mo_califica_el_apoyo_rec==='1'){
            ApiData.calificacionApoyo.excelente+=1;
          }else if(dato.c_mo_califica_el_apoyo_rec==='2'){
            ApiData.calificacionApoyo.bueno+=1;
          }else if(dato.c_mo_califica_el_apoyo_rec==='3'){
            ApiData.calificacionApoyo.regular+=1;
          }else if(dato.c_mo_califica_el_apoyo_rec==='4'){
            ApiData.calificacionApoyo.malo+=1;
          }else if(dato.c_mo_califica_el_apoyo_rec==='5'){
            ApiData.calificacionApoyo.deficiente+=1;
          }

          if(dato.el_tiempo_de_respuesta_de==='1'){
            ApiData.tiempoRespuesta.excelente+=1;
          }else if(dato.el_tiempo_de_respuesta_de==='2'){
            ApiData.tiempoRespuesta.bueno+=1;
          }else if(dato.el_tiempo_de_respuesta_de==='3'){
            ApiData.tiempoRespuesta.regular+=1;
          }else if(dato.el_tiempo_de_respuesta_de==='4'){
            ApiData.tiempoRespuesta.malo+=1;
          }else if(dato.el_tiempo_de_respuesta_de==='5'){
            ApiData.tiempoRespuesta.deficiente+=1;
          }

          if(dato.la_calidad_de_los_insumos==='1'){
            ApiData.calidadInsumos.excelente+=1;
          }else if(dato.la_calidad_de_los_insumos==='2'){
            ApiData.calidadInsumos.bueno+=1;
          }else if(dato.la_calidad_de_los_insumos==='3'){
            ApiData.calidadInsumos.regular+=1;
          }else if(dato.la_calidad_de_los_insumos==='4'){
            ApiData.calidadInsumos.malo+=1;
          }else if(dato.la_calidad_de_los_insumos==='5'){
            ApiData.calidadInsumos.deficiente+=1;
          }

          if(dato.los_mecanismos_de_comunica==='1'){
            ApiData.mecanismosComunicacion.excelente+=1;
          }else if(dato.los_mecanismos_de_comunica==='2'){
            ApiData.mecanismosComunicacion.bueno+=1;
          }else if(dato.los_mecanismos_de_comunica==='3'){
            ApiData.mecanismosComunicacion.regular+=1;
          }else if(dato.los_mecanismos_de_comunica==='4'){
            ApiData.mecanismosComunicacion.malo+=1;
          }else if(dato.los_mecanismos_de_comunica==='5'){
            ApiData.mecanismosComunicacion.deficiente+=1;
          }
          return true
        })
        setDataApi(ApiData);
        setApiLoad(true);
        console.log(ApiData)
      })
      .catch((e)=>{
        alert(e)
        console.error(e);
      })
      

  }, [])


  function setDatasetFunction(dataset){
    setDataset(dataset);
  }

  return (
    <>
      <Panel>
        <Banner />
        <Row style={{
          paddingLeft: '10px',
          paddingRight: '20px',
          paddingBottom: '50px'
        }}>
          <Col xs={24} sm={24} md={6} lg={6} xl={6}>
            <div style={{
              paddingTop: '50px'
            }}>
              <LoginComponent />
            </div>
          </Col>

          {/* Data set # 1 */}
          { dataset===1 ? 
          <Col xs={12} sm={12} md={18} lg={18} xl={18}>
            <Row justify="center">
              <h1>Plataforma de gestion inteligente UNAL</h1>
            </Row>
            <Row justify="center" style={{
              marginTop: '10px'
            }}>
              <Serach dataset={dataset} setDatasetFunction={setDatasetFunction}/>
            </Row>

            <Row justify="center">
              <Col xs={24} sm={24} md={12} lg={12} xl={12}>
                <div justify='center' style={{
                  height: '400px',
                  width: '400px'
                }}>
                  <Pie />
                </div>
              </Col>

              <Col xs={24} sm={24} md={12} lg={12} xl={12}>
                <div justify='center' style={{
                  height: '400px',
                  width: '400px'
                }}>
                  <Radar />
                </div>
              </Col>
            </Row>

            <Row justify='center'>
            <Col xs={24} sm={24} md={24} lg={24} xl={24}>
                <div justify='center' style={{
                  height: '400px',
                  width: '1000px'
                }}>
                  <Line />
                </div>
              </Col>
            </Row>


          </Col>
          : null }



            {/* Data set # 2 */}
            { dataset===2? 
          <Col xs={12} sm={12} md={18} lg={18} xl={18}>
            <Row justify="center">
              <h1>Plataforma de gestion inteligente UNAL</h1>
            </Row>
            <Row justify="center" style={{
              marginTop: '10px'
            }}>
                 <Serach dataset={dataset} setDatasetFunction={setDatasetFunction}/>
            </Row>


            <Row justify='center'>
            <Col xs={24} sm={24} md={24} lg={24} xl={24}>
                <div justify='center' style={{
                  height: '400px',
                  width: '1000px'
                }}>
                  <h3 style={{
                    textAlign: 'center',
                    marginTop: '30px'
                  }}>Percepción de servicios</h3>
                  <Bar2 
                  insumos={dataApi.calidadInsumos} 
                  comunicacion={dataApi.mecanismosComunicacion} 
                  apoyo={dataApi.calificacionApoyo} 
                  api={apiLoad}/>
                </div>
              </Col>
            </Row>
            <hr/>
            <Row justify="center" style={{marginTop: '20px'}}>
              <Col xs={24} sm={24} md={12} lg={12} xl={12}>
                <div justify='center' style={{
                  height: '400px',
                  width: '400px'
                }}>
                  <h3 style={{
                    textAlign: 'center'
                  }}>Tipo de usuario</h3>
                  <Pie2 data={dataApi.tipo_usuario} api={apiLoad}/>
                </div>
              </Col>

              <Col xs={24} sm={24} md={12} lg={12} xl={12}>
                <div justify='center' style={{
                  height: '400px',
                  width: '400px'
                }}>
                   <h3 style={{
                    textAlign: 'center'
                  }}>Tiempo de respuesta del laboratio</h3>
                  <Radar2 data={dataApi.tiempoRespuesta} api={apiLoad}/>
                </div>
              </Col>
            </Row>

            <hr/>
           
            <Row justify='center'>
            <Col xs={24} sm={24} md={24} lg={24} xl={24}>
                <div justify='center' style={{
                  height: '400px',
                  width: '1000px'
                }}>
                   <h3 style={{
                    textAlign: 'center'
                  }}>Implementos en laboratio</h3>
                  <Line2 />
                </div>
              </Col>
            </Row>


          </Col>
          : null }


          {/* Data set # 3 */}
          { dataset===3? 
          <Col xs={12} sm={12} md={18} lg={18} xl={18}>
            <Row justify="center">
              <h1>Plataforma de gestion inteligente UNAL</h1>
            </Row>
            <Row justify="center" style={{
              marginTop: '10px'
            }}>
                 <Serach dataset={dataset} setDatasetFunction={setDatasetFunction}/>
            </Row>


            <Row justify='center'>
            <Col xs={24} sm={24} md={24} lg={24} xl={24}>
                <div justify='center' style={{
                  height: '400px',
                  width: '1000px'
                }}>
                  <Line2 />
                </div>
              </Col>
            </Row>

            <Row justify='center'>
            <Col xs={24} sm={24} md={24} lg={24} xl={24}>
                <div justify='center' style={{
                  height: '400px',
                  width: '1000px'
                }}>
                  
                  <Bar3 />
                  
                </div>
              </Col>
            </Row>

            <Row justify="center">
              <Col xs={24} sm={24} md={12} lg={12} xl={12}>
                <div justify='center' style={{
                  height: '400px',
                  width: '400px'
                }}>
                  <Pie />
                </div>
              </Col>

              <Col xs={24} sm={24} md={12} lg={12} xl={12}>
                <div justify='center' style={{
                  height: '400px',
                  width: '400px'
                }}>
                  <Radar />
                </div>
              </Col>
            </Row>

    


          </Col>
          : null }

          


        </Row>
      </Panel>
      <Footer />

      <div>
        <ModalVideo channel='youtube' isOpen={isOpen} videoId='8f5ozJpz3wM' onClose={() => setIsOpen(false)} />
      </div>
    </>
  );
}

export default App;